.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item a {
  padding: 23px 0;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  white-space: nowrap;
  color: #060b35;
  align-items: center;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

a:hover {
  text-transform: uppercase;
  color: #00acdc;
}

.is-active, .active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 100%;
  height: 408px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  width: 100%;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card__title:hover {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__reviews {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews--count {
  color: #060b35;
  align-items: center;
  height: 16px;
  margin: 0 0 0 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 16px;
  display: flex;
}

.card__price--name {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

.card__price--number {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}

.card__buy {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 0 0 16px;
  padding: 12px 70px 12px 69px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 4px;
}

.main {
  perspective: 400px;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 487px) {
  .main {
    grid-template-columns: 200px;
    grid-template-rows: repeat(8 1fr);
  }
}

@media (width >= 488px) {
  .main {
    grid-template-columns: 200px 200px;
    grid-template-rows: repeat(4 1fr);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 200px 200px 200px;
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 200px 200px 200px 200px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}
